.BadgesList ul > li {
  margin: auto;
  margin-bottom: 0.5rem;
  width: 100%;
}

.BadgesListItem {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.23);
  border-radius: 5px;
  padding: 1rem;
  display: grid;
  grid-template-columns: 40% 60%;
  align-items: center;
}

.BadgesListItem__avatar {
  border-radius: 50%;
  background: #a3a3a3;
  margin-right: 0.5rem;
  width: 80px;
  height: 80px;
}

.list-grid {
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 1rem;
  gap: 1rem;
}

.filter {
  width: 100%;
  margin: 1rem auto 2rem;
}

@media (min-width: 601px) {
  .BadgesList ul > li {
    width: 75%;
  }
  .filter {
    width: 75%;
  }
}
@media (min-width: 840px) {
  .list-grid {
    grid-template-columns: 1fr 1fr;
  }
  .BadgesList ul > li {
    width: 100%;
  }
  .filter {
    width: 100%;
  }
}
@media (min-width: 1250px) {
  .list-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

